import React, { useState, useEffect, useRef } from 'react';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import styled from 'styled-components';
import { srConfig } from '../config.js';
import sr from '../utils/sr';
import { usePrefersReducedMotion } from '../hooks';

const StyledProjectsSection = styled.section`
  display: flex;
  flex-direction: column;
  align-items: center;

  ${'' /* h2 {
    font-size: clamp(24px, 5vw, var(--fz-heading));
  } */}
    ${'' /* margin-top: 10px; */}

  .head{
    margin-bottom: 34px;
    color: #ccd6f6;
    font-size: 30px;
    font-weight: 650;
  }
  .num{
    margin-bottom: 34px;
    margin-right:15px;
    color: #64ffda;
    font-size:  24px;
    font-weight: 500;
    margin-left: 40px;
  }

  .bor{
    margin-left:40px;
    border-color:#233554;
  }
  .card{
    background-color: #112240;
    border-radius: 10px;
    height: 190px;
    max-width: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 50px;
    flex: 1 0 21%; /* explanation below */
    margin: 20px;
  }

  svg{
    height: 100px;
    width: 89px;
    color: #64ffda;
    fill: #64ffda;
    margin-bottom: 20px;
  }

  .name{
    display: flex;
    justify-content: center;
    color:#ccd6f6
  }
  
  .card:hover{
    svg{
      color: #8892b0;
      fill: #8892b0;
    }
  }


  .cards{
    display: flex;
    flex-wrap: wrap;
    max-width: 1000px;
    margin-top: 50px;
  }
  h2{
    margin-top: 8px;
  }

`;


